//////////////
// containers
#partnering {
  max-width: 100%;
  width: 100%;
  .page-section {
    margin-bottom: 40px;
    max-width: 100%;
    width: 100%;
  }  
  .tile-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 10px 0 10px 0;
    max-width: 100%;
  
    &.how-to-partner {
    }
    &.full-width {
      width: 100%;
    }
    .tile {
      max-width: 100%;
      min-width: 275px;
      text-align: center;

      // title that goes inside tile
      .tile-title {
        margin: 0px auto 0px auto;;
        color: $default-text-color;
        padding-left: $content-body-padding;
        padding-right: $content-body-padding;
        font-weight: bold;
      }
    }
  }
  
  // title on top, then image on left, text on right
  .horizontal-tile {

    @media screen and (min-width: 850px) {
      width: 45%;
      margin: 0px 2% 30px 2%;
    }

    .horizontal-tile-contents {
      min-width: 275px;
    }

    // on mobile stack the image and the text
    .box-link {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      flex-direction: column;
    }

    .image_wrapper {
      display: inline-block;
      overflow: hidden;
      vertical-align: top; // or else img is on baseline, and leaves a whitespace at bottom
      width: 100%;
      height: 300px;

      .image_link_image {
        height: 300px;
        vertical-align: bottom; // or else img is on baseline, and leaves a whitespace at bottom
      } 
    }

    .link-description-wrapper {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin: 1rem;
      padding: 0 1rem;
    }

    .link-description {
      text-align: left;
    }

    // larger screens, put pic next to text
    @media screen and (min-width: 435px) {
      .horizontal-tile-contents {
        height: 300px;
      }
      .box-link {
        flex-direction: row;
      }
      .image_wrapper {
        min-height: 100%;
        width: 55%;
        .image_link_image { // have to set height or ...gets no height
        }
      }

      .link-description-wrapper {
        width: 45%;
        padding: 0;
        justify-content: center;
      }
    }
    // add button just on mobile so they know it's a link
    @media screen and (min-width: 850px) {
      .give-button {
        display: none;
      }
    }
  }
  
  // image with title and text beneath it
  .small-vertical-tile {
    width: 200px;
    margin: 10px;
    text-align: center;
    .image_link_image {
      display: inline-block;
      width: 100%;
    } 
    .link-description {
      padding: 10px;
    }
  }
  
  .section {
    padding: 0 20px;
    margin-bottom: 20px;
  }
  
  ////////////
  // components

  // title for pages
  .title {
    margin: 10px 0 20px 0;;
    font-size: 20px;
    color: $default-text-color;
    display: block;

    &.link {
      &:hover {
        color: $link-color;
      }
      
    }
  }
  
  // whole text box is a link
  .link-description-wrapper {
    color: $default-text-color; // otherwise becomes link color
    display: flex;
    &:hover {
      color: #717171; // so shows still link
    }

    .link-description {

    }
  }
  h1 {
    font-weight: bold;
    a {
      
    }
  }
  h2 {
    text-align: center;
  }
  .give-button {
    display: inline-block;
    margin: 10px;
    padding: 10px 20px;
    background-color: green;
    color: white;
    cursor: pointer;
    &:hover {
      cursor: pointer;
      background-color: #1d9d1d;
    }
  }
  .box-link {
    display: block;
    height: 100%;
    &:hover {
      color: #717171;
      .give-button {
        background-color: #1d9d1d;
      }
    }
  }
}



