main {
  width: inherit;
  margin: auto;

  .page-content {
    padding-top: $h1-font-size;
    padding-left: $content-body-padding;
    padding-right: $content-body-padding;
  }
  * {
    box-sizing: border-box;
  }

  @include breakpoint($content-body-width) {
    overflow: hidden;
    width: $content-width;
  }

  .article-title {
    text-align: center;
    margin: 0;
  }

  .tag-list {
    list-style-type: none;
  }
  .tag-labels {
    margin-left: .7rem; // centering
    text-align: left;
    
  }
  .tag-label {
    @include border-radius(13px);
    background: darken($feather-gray, 4);
    border: 0 none;
    color: $white;
    cursor: pointer;
    display: inline-block;
    font-size: 1.3rem;
    font-weight: bold;
    line-height: 1.3;
    margin-bottom: .8rem;
    margin-right: .7rem;
    padding: .4rem 1.2rem .5rem;
    text-align: center;
    &:hover {
      background: $link-color;
    }
    a {
      color: $white;
      width: 100%;
    }
  }

  .posted-date-wrapper {
    color: $gentle-gray;
    text-align: center;
    margin-bottom: 2rem;
    .posted-date {
      padding-left: .4em;
    }
  }

  // for the homepage and partner page mostly so far
  .tile_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 10px 0 100px 0;
    padding: 0 $content-body-padding;

    .tile {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 290px;
      margin: 10px 5px;
      @media screen and (min-width: 400px) {
      }

      .article_title {
        margin-bottom: 0;
        text-align: center;
        font-size: 2.5rem;

        a:hover {
          color: #36d5d5;
        }
      }

      div.article_subtitle {
        text-align: center;
        margin-top: -4px;
        margin-bottom: auto;
        line-height: 16px;
      }

      .image_link { //often will be a link, which contains an image
        display: inline-block;
        position: relative;
        width: 100%;
        height: 300px;
        overflow: hidden;

        img.image_link_image {
          min-height: 300px;
          width: 100%;
        }
      }
    }
  }

  // For the article show view, and several pages
  article {
    margin-bottom: 60px;
    padding: 0 $content-body-padding;

    .article-separator {
      margin-bottom: 100px;
    }

    .article-header-separator {
      margin-bottom: 50px;
      margin-top: 50px;
    }

    img {
      display: block;
      max-width: 90%;
    }

    /* Place here so that it doesn't mess with lists outside of articles */
    li {
      padding-left: 1.3em;
      // text-indent: -2em;
    }
    ol li {
      text-indent: initial; // we are overwriting for li within ul though
    }
  }

  .archive-list {
    list-style-type: none;
    list-style: none;

    .archive-posted-date-wrapper {
      color: $gentle-gray;
      margin-right: .4em;
      .posted-date {
        padding-left: .4em;
      }
    }
  }

  //////////////////////
  // Image containers //

  // image group - 2 or more images put together. On phones, will shrink images so that they can stay in a row.
  // NOTE add .wrap if you want group to wrap on phone. 
  // see 2019/09/15 update for putting large group with captions
  // see 2021/10/03 update for vertical group
  span.image_group {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 20px auto;

    &.wrap {
      flex-wrap: wrap;
    }
    &.vertical-group {
      flex-direction: column;
    }

    // keeping this separate from align-left and align-right, which is for internal flexbox stuff
    &.right {
      float: right;
      // add margin back in, since now there's going to be content on one side or the other
      margin: 20px 20px;
    }
    &.left {
      float: left;
      // add margin back in, since now there's going to be content on one side or the other
      margin: 20px 20px;
    }

    // so you can make these image groups not width=100%. Particularly useful when it's a vertical-group
    &.xs, &.sm, &.med, &.lg, &.xl {
      
      // make sure to override max-width of child image-containers or else they won't be full width of this image-group
      .image_container {
        max-width: 100%;
      }
    }
    &.xs {
      // min-width: 190px; had this on originally; but decided to take it off ehre rather than set it for all image containers
      width: 200px;
    }
    &.sm {
      width: 300px;
    }
    &.med {
      width: 380px;
    }
    &.lg {
      width: 500px;
    }
    &.xl {
      width: 750px;
    }
  }
  span.image_group.align-bottom {
    align-items: flex-end;
  }
  span.image_group.align-center {
    align-items: center;
  }
  span.image_group.align-top {
    align-items: flex-start;
  }
  span.image_group.stretch {
    align-items: stretch;
  }

  //get all images same height
  span.image_group.same-height-md {
    max-height: 160px;
    overflow-y: hidden;
    .image_container img {
      top: 0;
    }
  }

  span.small {
    font-size: 1rem;
  }

  .image_container {
    max-width: 90%;
    width: 100%;
    text-align: center;
    font-style: italic;
    margin: 0 5px 10px 5px;
    overflow: hidden;
    display: inline-block;

    img {
      display: block;
      max-width: 98%;
      margin: 0 auto;
    }
  }


  // location
  .image_container.center {
    margin: 20px auto 20px auto;
    display: block;
  }
  .image_container.left {
    @media screen and (min-width: 400px) {
      max-width: 48%;
      min-width: 190px; 
      display: inline-block;
      float: left;
      margin: 0px 15px 10px 0;
    }
    
  }
  .image_container.right {
    @media screen and (min-width: 400px) {
      max-width: 48%;
      min-width: 190px; 
      display: inline-block;
      float: right;
      margin: 0px 0 10px 15px;
    }
  }

  // sizing 
  .image_container.xs {
    // min-width: 190px; had this on originally; but decided to take it off ehre rather than set it for all image containers
    width: 200px;
  }
  .image_container.sm {
    width: 300px;
  }
  .image_container.med {
    width: 380px;
  }
  .image_container.lg {
    width: 500px;
  }
  .image_container.xl {
    width: 750px;
  }


  // for when image container is a banner
  .image_container.banner {
    width: 100%;
    max-width: 100%;
    height: 200px;
    overflow: hidden;
    margin-right: 0;
    margin-left: 0;
  }
  .read-more {
    @include clearfix;
    font-size: 1.5rem;
    margin-top: 2rem;
    padding-right: $content-body-padding;
    a {
      float: right;
    }
  }
  .movie-wrapper {
    margin: 40px auto;
    padding: 0 $content-body-padding;
    width: 90%;
    @include breakpoint($content-body-width) {
      width: 640px;
    }
    .movie {
      position: relative;
      width: 100%;
      iframe {
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
      }
    }
  }
  .paginate-wrapper {
    @include pie-clearfix;
    font-size: 1.5rem;
    margin: 0 4rem;
    .previous-page {
      float: left;
    }
    .next-page {
      float: right;
    }
  }
  .page-number {
    color: $gentle-gray;
    font-size: 1.5rem;
    margin-top: 3rem;
    text-align: center;
  }
  .box-link {
    border: 1px solid #e5e5e5;
    cursor: pointer;
    box-sizing: border-box;
    &:hover {
      box-shadow: 0 0 8px 0 rgba(0,0,0,.3);
      .give-button {
        cursor: pointer;
        background-color: #1d9d1d;
      }
    }

  }
}
#article-comments {
  padding-left: $content-body-padding;
  padding-right: $content-body-padding;
  margin: 30px 20px;
}



