// MIX-INS
@mixin font-smoothing($value: antialiased) {
  @if $value == antialiased {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
  } @else {
    -moz-osx-font-smoothing: auto;
    -webkit-font-smoothing: subpixel-antialiased;
  }
}

@viewport {
  width: device-width;
  zoom: 1;
  min-zoom: 1;
  max-zoom: 3;
  user-zoom: fixed;
}

* {
  // @include selection($beam-gray, $white)
}

html {
  background: $blind-gray;
  font-size: $em-normalize-ratio;
  width: 100%;
}

body {
  @include font-smoothing;
  background-color: $default-background-color;
  color: $default-text-color;
  font-family: $default-font-family;
  font-size: $default-font-size;
  line-height: $default-line-height;
  box-sizing: border-box;

  /* for the article header links */ 
  i.show-on-hover {
    display: none;
    position: absolute;
  }
  
  h1:hover .show-on-hover,
  h2:hover .show-on-hover {
    display: inline-block;
  }

  iframe {
    margin-bottom: 2rem;
    max-width: 100%;
  }
}

h1 {
  font-size: $h1-font-size;
  line-height: $h1-line-height;
  margin: $h1-font-size 0;

  a {
    color: $default-text-color;

    &:hover {
      color: $link-color;
    }
  }
}

h2 {
  font-size: $h2-font-size;
  line-height: $h2-line-height;
  // removed this margin before, don't know why we did, but now need some margin for at least the articles, so adding below
  //margin: $h2-font-size + .8rem 0;
  word-wrap: break-word;
}
.page-content article h2 {
  margin: 1rem 0;
}

h3 {
  font-size: $h3-font-size;
  font-weight: bold;
  line-height: $h3-line-height;
  margin: $h3-font-size 0;
  word-wrap: break-word;
}
h5 {
  text-decoration: underline;
}

h6 {
  text-align: center;
}
p {
  margin: 0 0 $default-font-size + 1.0rem 0;
  word-wrap: break-word;
}

hr {
  background: $border-gray;
  border: 0;
  height: 1px;
  margin: 24px 0;
}

ul {
  list-style: disc;
  margin-bottom: $default-font-size + 1rem;
  margin-left: 2rem;
  list-style-position: inside;
  ul {
    margin-bottom:0;
    list-style-type: circle;
  }
  ol {
    margin-bottom: 0;
  }
}

// To make up for the extra margin after every paragraph:
p + ul {
  margin-top: -2.3rem;
}

ol {
  list-style-type: decimal;
  margin-bottom: $default-font-size + 1rem;
  margin-left: 2rem;


  ul {
    margin-bottom:0;
  }
  ol {
    margin-bottom: 0;
  }
}

a {
  color: $link-color;
  text-decoration: none;
 
  &:hover {
    color: $hover-link-color;
  }
}

pre {
  background: $code-gray;
  border: 1px solid $whity-light-gray;
  font-family: $monospace;
  font-size: 1.3rem;
  line-height: 1.5;
  margin: 3rem auto;
  overflow: scroll;
  padding: 1.4rem;

  @include breakpoint($large-screen) {
    font-size: 1.4rem;
  }
}

code {
  font-family: $monospace;
}

strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

blockquote {
  border-left: 3px solid $whity-light-gray;
  color: lighten($default-text-color, 20);
  margin: 3rem 1rem;
  padding-left: 1rem;
}

table {
  margin-bottom: $default-font-size + 1rem;
  margin-top: $default-font-size + 1rem;

  tr {
    background-color: $white;
    border-top: 1px solid $whity-gray;
    margin: 0;
    padding: 0;

    &:nth-child(2n) {
      background-color: $code-gray;
    }

    th {
      border: 1px solid $whity-gray;
      font-weight: bold;
      margin: 0;
      padding: 6px 13px;
      text-align: left;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    td {
      border: 1px solid $whity-gray;
      margin: 0;
      padding: 6px 13px;
      text-align: left;
    }

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}





