.social {
  margin: 40px auto 30px;
  padding-left: $content-body-padding;
  padding-right: $content-body-padding;

  ul {
    @include horizontal-list;
    li {
      font-size: 3.2rem;
      list-style-type: none;
      margin-right: .5rem;
      a {
        color: $default-text-color;

        &:hover {
          color: $link-color;
        }
      }
    }
  }
}



