header {
  margin: 0 auto;

  .top-menu {
    @include pie-clearfix;
    background-color: $blind-gray;
    height: $top-menu-height;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;

    @include breakpoint($mobile-screen) {
      height: $top-menu-height-mobile;
    }
    @include breakpoint($non-mobile-screen) {
      height: $top-menu-height;
    }

    .top-menu-wrapper {
      color: $top-menu-text-color;
      margin: 0 auto;
      width: 100%;

      @include breakpoint($large-screen) {
        width: $content-width;
      }

      a {
        color: $top-menu-text-color;

        &:hover {
          color: $whity-light-gray;
        }
      }

      .top-menu-list {
        margin-left: 15px;
        display: flex;
        flex-flow: row wrap;
        align-content: space-between;
        justify-content: space-between;

        @include breakpoint($large-screen) {
          margin-left: 30px;
        }

        ul {
          @include horizontal-list;
          padding-top: 1rem;

          li {
            font-size: 1.2rem;
            margin: 0;

            &:after {
              content: '\2219';
              margin-left: 10px;
              margin-right: 3px;
            }

            &:last-child:after { content: ''; }
          }
        }
        ul.right_menu {
          @include breakpoint($mobile-screen) {
            width: 100%;
          }
        }
      }

      .feed {
        float: right;
        line-height: 2.2rem;
        text-align: right;
        width: 10%;

        @include breakpoint($large-screen) {
          margin-right: 60px;
          text-align: right;
        }

        i {
          font-size: 1.8rem;
        }
      }
    }
  }

  .logo {
    margin: 40px auto 0;
    @include breakpoint($mobile-screen) {
      margin-top: $top-menu-height-mobile;
    }
    padding: 0px;
    min-height: 140px;
    min-width: 100%;
    min-width: 100%;
    height: 120px;
    background-image: url('../images/cambodia/preah_vihear/ratana_house.pano.banner.jpg');
    background-position: left center;
    background-size: cover;
    

    @include breakpoint($large-screen) {
      height: 220px;
      background-position: center center;
    }
  }
  .logo-contents-container {
    max-width: $content-body-width;
    height: 100%;
    position: relative;
    margin: auto;
    
  

    .logo-text {
      color: $default-text-color;
      //background-color: rgba(255, 255, 255, .4);
      position: absolute;
      top: 0px;
      right: 0;
      font-size: 30px;
      padding: 0 1rem;
      margin: 0 auto;
      text-align: right;
      @include breakpoint($large-screen) {
        font-size: 4.5rem;
      }
    }
  }

  a:hover {
    text-decoration: underline;
  }
}



